import axios from 'axios';
import { baseUrl, months, recentArticles, isLocal, localBaseUrl } from '../constants/constants';

export let articleObject = {};

export const getDate = (date) => {
  if (!date) return new Date();
  return new Date(date);
};

export const printDate = (date) => {
  if (!date) return '';
  date = getDate(date);
  const month = months[date.getMonth()];
  return `${month} ${date.getDate()}, ${date.getFullYear()}`;
};

export const getRecentArticles = () => {
  const url = isLocal ? `${localBaseUrl}/${recentArticles}` : `${baseUrl}/api/articles/${recentArticles}.json`;
  return axios.get(url);
};

export const getBlog = (id) => {
  const random = Math.ceil(Math.random() * 100000);
  const url = isLocal ? `${localBaseUrl}/api/get_article/${id}` : `${baseUrl}/api/articles/${id}.json?clearCache=${random}`;
  return axios.get(url);
};

export const getBlogsByCategory = (id) => {
  if (isLocal) {
    return axios.post(`${localBaseUrl}/api/get_blog_list`, { catId: id });
  }
  return axios.get(`${baseUrl}/api/categories/${id}.json`);
};

export const getArticleMap = () => {
  return axios.get(`${localBaseUrl}/api/get_article_map`)
    .then((res) => {
      articleObject = res.data.data;
      debugger;
      return res.data.data;
    })
    .catch((err) => {
      console.log(err);
    });
};

if (isLocal)
  getArticleMap();

export const getArticleUrl = (id) => (process.env.ENV === 'development' ? `/blog/${articleObject.articleIdMap[id]}` : `/blog/${id}`);
